

    .bordered {
      border: 1px solid #0764bc;
      border-radius: 4px;
      position: relative;
      box-shadow: 0 0 2px 0 #0e325f, inset 0 0 2px 0 #0e325f;
      background: #0f113a;

    }
    .bordered::before {
        pointer-events: none;
        content: '';position: absolute;left: 0;right: 0;bottom: 0;top: 0;
        border-radius: 4px;


    }



    .籍贯{
      height: 315px;
      display: flex;
      flex-direction: column;
      align-items: center;



    }
    .籍贯>.charts {
          width: 100%;flex: 1;
          display: flex;
      }
    .籍贯>.chart {
         flex: 1;
         width: 100%;
     }
    .籍贯>h2 {
        flex-shrink: 0;
        border: 1px solid #0a5299;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        font-size: 22px;
        line-height: 24px;
        padding: 10px 28px;
        text-shadow: 0 0 3px white;
    }
    .籍贯 {
      height: 753px; background: transparent;

    }
    .籍贯> .wrapper {
        position: relative;width: 100%;flex: 1;
        display: flex; flex-direction: column;}
    .籍贯> .wrapper>.chart {flex: 1;}
    .籍贯> .wrapper>.legend {
        position: absolute;top: 40px;left: 20px;display: flex;
        align-items: center;
        font-size: 22px; padding: 20px 10px 20px 0;
        border-radius: 0;}
    .籍贯> .wrapper>.icon {
        margin: 0 10px;
        display: inline-block;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: #1cecd9;
    }

    .籍贯> .wrapper>.notes {
        position: absolute;
        bottom: 10px;
        right: 10px;
    }
